@media (min-width: 1401px) {

    .homepage_content{
      width: 1600px;
    } 

    .section{
        min-height: 500px;
    }

    .section_text{
        font-size: clamp(18px,3.25vw,18px);
    }

        
    .intro_title{
        padding: 2%;
        font-size: clamp(32px,3.25vw,32px);
        color: white;
        font-weight: bold;
    }
      
    .intro_message{
        padding: 3%;
        font-size: clamp(22px,3.25vw,22px);
        color: white;
    }
    
    .Section_Subtitle1{
        font-size: 32px;
        text-shadow:   -2px -2px 0 rgb(249, 248, 248),  
        1px -1px 0 rgb(239, 239, 239),
        -1px 1px 0 rgb(255, 255, 255),
        1px 1px 0 rgb(255, 255, 255),
        1px 1px 2px #ffffff,
        1px 1px 3px #ffffff,
        1px 1px 5px #ffffff,
        1px 1px 7px rgb(255, 255, 255);
            color:rgb(144, 17, 10);
      
        margin-bottom: 2%;
        font-weight: bold;
    }

    .section2{
        width: 80%;
        padding-top: 3%;
        padding-bottom: 3%;
        padding-left: 5%;
        padding-right: 5%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
      }

      
  }